export function setCookie(name: string, value: string, props?: { expires?: number }) {
  const additionalSettings: { [key: string]: any } = {};

  let expiresSeconds = props?.expires;

  if (expiresSeconds && typeof expiresSeconds == 'number') {
    const date = new Date();
    date.setTime(date.getTime() + expiresSeconds * 1000);
    additionalSettings.expires = date.toUTCString();
  }

  const encodedValue = encodeURIComponent(value);
  let updatedCookie = name + '=' + encodedValue;

  for (const propName in additionalSettings) {
    updatedCookie += '; ' + propName;
    const propValue = additionalSettings[propName];
    if (propValue !== true) {
      updatedCookie += '=' + propValue;
    }
  }

  document.cookie = `${updatedCookie}; path=/`;
}

export function deleteCookie(name: string) {
  setCookie(name, '', { expires: -1 });
}


